<template>
  <div class="home-wrap" v-loading="pageLoading">
    <!-- 用户概述 begin  -->
    <div style="overflow:hidden">
      <div class="account-tab">
        <div class="account-tab-title">用户概述</div>
        <div class="account-tab-content">
          <i><img src="../../assets/user.png"></i>
          <p>
            <span>{{ userDetail.user_name }}</span>
          </p>
          <p>账号数量：
            <span>{{ userDetail.total_num }}</span>
          </p>
        </div>
      </div>
      <div class="account-tab">
        <div class="account-tab-title">当前账号</div>
        <div class="account-tab-content">
          <p style="padding-left:0">待审核广告：
            <span style="color:blue;font-weight:bold" v-loading='numLoading'>{{ adDetailList.pending_ad_num }}</span>
          </p>
          <p>未通过广告：
            <span style="color:blue;font-weight:bold" v-loading='numLoading'>{{ adDetailList.denied_ad_num }}</span>
          </p>
          <p>有效广告：
            <span style="color:blue;font-weight:bold" v-loading='numLoading'>{{ adDetailList.normal_ad_num }}</span>
          </p>
        </div>
      </div>
    </div>
    <!-- 用户概述 end   -->
    <!-- table begin -->
    <div class="temp-item-inlineblock">
      <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
      </el-date-picker>
    </div>
    <el-table v-loading="loading" :data="userDetail.list" style="width: 100%">
      <el-table-column prop="account_id" label="ID">
      </el-table-column>
      <el-table-column prop="qq" label="QQ">
      </el-table-column>
      <el-table-column prop="account_name" label="名称">
      </el-table-column>
      <el-table-column prop="corporation_name" label="企业名称">
      </el-table-column>
      <el-table-column key="daily_budget" label="日限额(元/天)">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.daily_budget / 100 }}</span>
            <i class="el-icon-edit" @click="handleListDailyBudgetChange(scope.row)"></i>
          </template>
      </el-table-column>
      <el-table-column prop="realtime_cost" :formatter="unitConversionPercent" label="今日消耗(元)">
      </el-table-column>
      <el-table-column prop="general_cash_balance" :formatter="unitConversionPercent" label="现金账户(元)">
      </el-table-column>
      <el-table-column prop="general_gift_balance" :formatter="unitConversionPercent" label="虚拟账户(元)">
      </el-table-column>
      <el-table-column prop="credit_roll_balance" :formatter="unitConversionPercent" label="信用账户(元)">
      </el-table-column>
      <el-table-column prop="impression" :formatter="unitConversion" label="曝光量(次)">
      </el-table-column>
      <el-table-column prop="click" :formatter="unitConversion" label="点击量(次)">
      </el-table-column>
      <el-table-column prop="ctr" label="平均点击率">
      </el-table-column>
      <el-table-column prop="cost" :formatter="unitConversionPercent" label="花费(元)">
      </el-table-column>
    </el-table>
    <!-- table end   -->
  </div>
</template>

<script>
export default {
  created() {
    this.getDetail()
    if (this.account_id) {
      this.getAdDetail()
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getAdDetail()
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  methods: {
    // 日期
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.params.start_date =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.params.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
      this.getDetail()
    },
    // 获取列表
    getDetail() {
      var that = this
      that.loading = true
      this.$axios
        .get('/Admin/Marketing_Account/getDetail', {
          params: that.params
        })
        .then(function(response) {
          that.pageLoading = false
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            that.userDetail = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.pageLoading = false
          that.loading = false
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    unitConversionPercent(row, column) {
      if(row[column.property] == '-'){
        return row[column.property]
      }
      var val = row[column.property] / 100
      val = (val + '').replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
      return val
    },
    unitConversion(row, column) {
      if(row[column.property] == '-'){
        return row[column.property]
      }
      var val = row[column.property]
      val = (val + '').replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
      return val
    },
    getAdDetail(){
      var that = this
      that.numLoading = true
      let params = { 'account_id': this.account_id}
      this.$axios
      .get('/Admin/Marketing_Account/getAdDetail', {
        params: params
      })
      .then(function(response) {
        that.numLoading = false
        var respData = response.data
        if (respData.code == 0) {
          that.adDetailList = respData.data
        } else {
          that.$message(respData.msg)
        }
      })
      .catch(function(err) {
        that.numLoading = false
        that.$message('网络繁忙，请稍后再试！')
      })
    },
    
    // 修改日限额
    handleListDailyBudgetChange(row) {
      var oldval = row.daily_budget / 100
      var that = this
      this.$prompt('请输入日限额(元 / 天)', '编辑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^([1-9]\d*)+(.\d{1,2})?$|^0\.\d{1,2}$/,
        inputErrorMessage: '请输入有效值'
      })
        .then(({ value }) => {
          if(value - oldval >= 50 || oldval - value >= 50){
            that.setAdvertiserUpdate(row, 'daily_budget', value, oldval*100 )
            row.daily_budget = value * 100
          }else{
            that.$message.error('每次修改幅度不能低于50元')
          }
        })
        .catch(() => {
        })
    },
    setAdvertiserUpdate(row, key, value, oldval) {
      var that = this
      var params = new Object()
      params[key] = value
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Advertiser/update', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code != 0) {
            that.$message.error(respData.msg)
            row[key] = oldval
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          row[key] = oldval
        })
    },
  },
  data() {
    return {
      userDetail: {},
      loading: false,
      pageLoading: true,
      numLoading: true,
      adDetailList:{
        pending_ad_num : '-',
        denied_ad_num : '-',
        normal_ad_num : '-',
      },
      params: {
        start_date:'',
        end_date:''
      },
      pickerDate: [new Date(), new Date()],
      pickerOptions2: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去7天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去30天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      }
    }
  }
}
</script>


<style scoped>
.home-wrap {
  padding: 30px 46px;
}
.home-wrap .account-tab {
  float: left;
  box-sizing: border-box;
  background: #f2f6fc;
  width: 50%;
  border-radius: 4px;
  margin-bottom: 10px;
}
.home-wrap .account-tab .account-tab-title {
  padding: 10px 20px;
  color: #888;
  font-size: 14px;
  background: #ebeef5;
  border-radius: 4px 4px 0 0;
}

.home-wrap .account-tab .account-tab-content {
  padding: 20px 20px;
  overflow: hidden;
}
.home-wrap .account-tab .account-tab-content i {
  display: inline-block;
  float: left;
}
.home-wrap .account-tab .account-tab-content i img {
  width: 32px;
}
.home-wrap .account-tab .account-tab-content p {
  float: left;
  display: inline-block;
  line-height: 32px;
  color: #888;
  padding: 0 20px;
  font-size: 14px;
}
.home-wrap .account-tab .account-tab-content p span {
  font-size: 16px;
  color: #666;
}
</style>



